.todo-header input{
    width: 583px;
    height: 28px;
    font-size : 14px;
    border: 1px solid #ccc;
    border-radius : 4px;
    padding: 4px 7px;
    margin: 20px auto 0;
}

.todo-header input:focus{
    outline: none;
    border-color: rgba(82, 168,236,0.8);
    box-shadow: insert 0 1px 1px rgba(0,0,0,0.075), 0 0 8px rgba(82, 168,236,0.8);
}